body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 0 0 20px;
}
form #submit_btn {
  border-top-right-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
}
#todoList [type="text"] {
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
}
#todoList .completed {
  text-decoration: line-through;
}
#todoList .custom-checkbox {
  padding-left: 1.1rem;
}
#todoList .custom-checkbox label {
  display: block;
  position: absolute;
  left: -6px;
  top: 0;
  width: 24px;
  height: 38px;
  cursor: pointer;
}
#todoList .custom-checkbox label:after,
#todoList .custom-checkbox label:before {
  top: 0.75rem;
  left: -0.1rem;
}
#todoList .btn-danger {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
}
.invalid-feedback {
  position: absolute;
  bottom: -23px;
  font-size: 14px;
}
